*,
*::after,
*::before {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}
body {
   background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
   color: white;
   font-family: "Poppins", sans-serif;
   min-height: 100vh;
}
.App {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
}
@media only screen and (max-width: 500px) {
   html {
      font-size: 62.5%;
   }
}
@media only screen and (max-width: 600px) {
   form {
      justify-content: center;
      flex-direction: column;
      gap: 2rem;
   }
   .form__input {
      display: flex;
   }
}
header {
   font-size: 2rem;
}

header,
form {
   min-height: 20vh;
   display: flex;
   justify-content: center;
   align-items: center;
}
form input,
form button {
   padding: 0.5rem;
   font-size: 1.4rem;
   border: none;
   background: white;
}
form button {
   color: #ff6f47;
   background: #f7fffe;
   cursor: pointer;
   transition: all 0.3s ease;
}
form button:hover {
   background: #ff6f47;
   color: white;
}
.todo-container {
   display: flex;
   justify-content: center;
   align-items: center;
}

.todo-list {
   min-width: 30%;
   list-style: none;
}

.todo {
   min-width: 70vw;
   margin: 0.5rem;
   background: white;
   font-size: 1.5rem;
   color: black;
   display: flex;
   justify-content: space-between;
   align-items: center;
   transition: all 1s ease;
}

.todo li {
   flex: 1;
   word-break: break-all;
}

.trash-btn,
.complete-btn {
   background: #ff6f47;
   color: white;
   border: none;
   padding: 1rem;
   cursor: pointer;
   font-size: 1rem;
}
.complete-btn {
   background: rgb(11, 212, 162);
}
.todo-item {
   padding: 0rem 0.5rem;
}

.fa-trash,
.fa-check {
   pointer-events: none;
}

.fall {
   transform: translateY(10rem) rotateZ(20deg);
   opacity: 0;
}

.completed {
   text-decoration: line-through;
   opacity: 0.5;
}

/*CUSTOM SELECTOR */
select {
   -webkit-appearance: none;
   -moz-appearance: none;
   -ms-appearance: none;
   appearance: none;
   outline: 0;
   box-shadow: none;
   border: 0 !important;
   background-image: none;
}

/* Custom Select */
.select {
   margin: 1rem;
   position: relative;
   overflow: hidden;
}
select {
   color: #ff6f47;
   font-family: "Poppins", sans-serif;
   cursor: pointer;
   width: 14rem;
   height: 2.5rem;
   padding: 0rem 1rem;
}
/* Arrow */
.select::after {
   content: "\25BC";
   position: absolute;
   top: 0;
   right: 0;
   display: flex;
   justify-content: center;
   align-items: center;
   padding: 0.7rem;
   height: 100%;
   background: #ff6f47;

   cursor: pointer;
   pointer-events: none;
}
